<template>
	<div id="app">
		<!-- <pre v-html="highlightCode"></pre>
		<div v-html="styleEditor"></div> -->
    <StyleEditor :code="currentStyle" ref="styleEditor"></StyleEditor>
    <ResumeEditor :fullResume="currentResume" :enableHtml="isHtml" ref="resumeEditor"></ResumeEditor>
	</div>
</template>



<script>

  import StyleEditor from './components/styleEditor1.vue'
  import ResumeEditor from './components/resumeEditor1.vue'
	import Prism from 'prismjs'

	export default {
     name:'app',
		components:{
      StyleEditor,ResumeEditor
    },
      
		data (){
			return {
        interval:40,
        isHtml:false,
				currentStyle:'',
				fullStyle:[
        `/*
* Inspired by http://strml.net/
* Hi 你好！我是肖俊
* 这是我制作的的电子版个人简历！
* 感谢您的耐心观看！
*/

/* 首先给所有元素加上过渡效果 */
* {
  transition: all .3s;
}
/* 白色背景太单调了，我们来点背景 */
html {
  color: rgb(222,222,222); 
  background: rgb(0,43,54);
}
/* 文字离边框太近了 */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  margin: .5em;
  overflow: auto;
  width: 45vw; height: 90vh;
}
/* 代码高亮 */
.token.selector{ color: rgb(133,153,0); }
.token.property{ color: rgb(187,137,0); }
.token.punctuation{ color: yellow; }
.token.function{ color: rgb(42,161,152); }

/* 加点 3D 效果呗 */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
  transform: rotateY(10deg) translateZ(-100px) ;
}

/* 接下来我给自己准备一个编辑器 */
.resumeEditor{
  position: fixed; right: 0; top: 0;
  padding: .5em;  margin: .5em;
  width: 48vw; height: 90vh;
  border: 1px solid;
  background: white; color: #222;
  overflow: auto;
}
/* 好了，我开始写简历了 */


`,
          `
/* 这个简历好像差点什么
 * 对了，这是 Markdown 格式的，我需要变成对 HR 更友好的格式
 * 简单，用开源工具翻译成 HTML 就行了
 */
`
          ,
          `
/* 再对 HTML 加点样式 */
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul>li::before{
  content: '•';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
  overflow: auto;
}
`
      ],
      currentResume:'',
      fullResume:`
肖俊
----

求职意向：前端工程师，js工程师

联系方式
-----

* 电话：18627156370
* 邮箱：313253805@qq.com

教育背景
-----

西南科技大学   通信工程(本科)

工作经历
-----
2018 - 至今  武汉迅思维科技有限公司   前端工程师/全栈工程师
* 公司安防直播录像平台的开发和部署维护
* 公司产品web页面的制作

2015 - 2017 北京宜通华瑞科技有限公司  运维工程师
* linux服务器的安装调试及日常维护
* 运营商网络服务器的远程维护

项目经验
----

> 安防直播录像平台
>
> 项目简介：本项目主要是搭建流媒体服务器，将视频推送公网实现在线直播功能。
> 
> 使用技术：本项目前端web页面基于react和ant design实现，
> nginx作为web服务器使用rtmp插件nginx-rtmp-module实现流媒体服务器功能，
> 后端使用了node的koa框架实现后台api接口，通过nginx做接口转发，
> 数据库使用的是mongodb,通过mongoose工具操作数据库。
>
> 实现功能：本项目实现平台查看直播视频及录像等功能，
> 管理员可以查看平台所有在推流的视频和录像，普通用户只能观看指定视频及录像。

职业技能
----

* 熟悉linux系统开发环境，熟悉shell vim等工具
* 熟悉使用开发中的各种工具，如chrome调试，postman，webpack等
* 熟练使用前端框架react和vue进行web开发
* 熟悉nodejs，使用express和koa框架进行后台开发
* 熟悉mongodb，通过mongoose工具操作数据库中的数据
* 了解docker技术，熟练使用docker在服务器上部署软件
* 了解go语言和lua脚本

链接
----

* [GitHub](https://github.com/x313253805)
`
		 }
		},
    created(){
      this.makeResume();
    },
    methods:{
      makeResume:async function(){
        await this.progressSlowStyle(0)
        await this.progressSlowResume()
        await this.progressSlowStyle(1)
        await this.showHtml()
        await this.progressSlowStyle(2)
      },
      progressSlowStyle(n){
        return new Promise((resolve,reject)=>{
          let interval = this.interval;

          let showStyle = ()=>{
            let style = this.fullStyle[n];
            if(!style){return}
            // 计算前n个字符总数
            // console.log(style.length);
            let length = this.fullStyle.filter((_,index)=>index<=n ).map((item)=>item.length).reduce((p,c)=>p+c,0);
            // console.log(length);
            let prefixLength = length - style.length;

            // console.log(length,prefixLength);
            if(this.currentStyle.length < length){
              let l = this.currentStyle.length - prefixLength
              let char = style.substring(l,l+1) || '';
              this.currentStyle += char;
              // console.log(l);
              if(style.substring(l-1,l) === '\n' && this.$refs.styleEditor ){
                this.$nextTick(()=>{
                  this.$refs.styleEditor.goBottom();
                })
              }

              setTimeout(showStyle,interval);
            }else{
              resolve()
            }
          }
          showStyle()
         })
        
      },
      progressSlowResume(){
        return new Promise((resolve,reject)=>{
         let length = this.fullResume.length;
         let interval = this.interval;
         let showResume = ()=>{
           if(this.currentResume.length < length){
             this.currentResume = this.fullResume.substring(0,this.currentResume.length+1);
             let lastchar = this.currentResume.substring(this.currentResume.length -2 ,this.currentResume.length-1)
             if(lastchar === '\n' && this.$refs.resumeEditor){
                this.$nextTick(()=>{
                  this.$refs.resumeEditor.goBottom();
                })
             }
               setTimeout(showResume,interval);

            }else{
              resolve()
            }
          }
         showResume();
        })
      },
      showHtml(){
        return new Promise((resolve,reject)=>{
          this.isHtml = true;
          this.$refs.resumeEditor.goTop();
          resolve()
        })
      }

    }
	}

</script>

<style scoped>
 #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100vh; position: relative;
  }

  html {
    min-height: 100vh;
  }
  *{
    box-sizing: border-box;
  }
</style>



