<template>
    <div class="styleEditor" ref="container">
        <pre v-html="highlightCode"></pre>
        <div v-html="styleEditor"></div>
    </div>
</template>

<script>

import Prism from 'prismjs'

export default {
    props:['code'],
    data (){
      return {
        }
    },
    computed:{
        highlightCode(){
            return Prism.highlight(this.code,Prism.languages.css);
        },
        styleEditor(){
            return '<style>' + this.code + '</style>'
        }
    },
    methods:{
        goBottom(){
            this.$refs.container.scrollTop = 10000;
        }
    }
}
</script>

<style scoped>

</style>





