<template>
  <div class="resumeEditor" :class="{htmlMode:enableHtml}" ref="container">
      <pre v-if="enableHtml" v-html="myResume"></pre>
      <pre v-else v-html="myResume"></pre>
  </div>
</template>


<script>
import marked from 'marked'
export default {
  props:['fullResume','enableHtml'],
  data (){
      return {
      }
  },

  computed:{
      resumeEditor(){
          return '<style>' + this.fullResume + '</style>';
      },
      myResume(){
          return this.enableHtml ? marked(this.fullResume):this.fullResume
      }
  },
  methods:{
      goBottom(){
          this.$refs.container.scrollTop = 10000;
      },
      goTop(){
          this.$refs.container.scrollTop = 0;
      }
  }
}
</script>



<style scoped>
.htmlMode {
    animation: flip 2s;
    
}

@keyframes flip {
        from{
            opacity: 0;
        }
        to {
            opacity: 1;
        }
}
</style>




